import { faShoppingCart } from "@fortawesome/free-solid-svg-icons";
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import NavItem from "./SubComponents/NavItem";
import NavItemDropDown from "./SubComponents/NavItemDropDown";

function Header() {
  
  const pages_links = [
    {
      name: 'Contact One',
      url: '#'
    },
    {
      name: 'Contact Two',
      url: '#'
    }
  ];

  const blog_links = [
    {
      name: 'Blog Default',
      url: '#'
    },
    {
      name: 'Blog Blog',
      url: '#'
    }
  ];

  const course_links = [
    {
      name: 'Course Gird 1',
      url: '#'
    },
    {
      name: 'Course Gird 2',
      url: '#'
    }
  ];

  return (
    <div className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container">
        <div className="row w-100">
          <div className="col-lg-9 navbar pt-0 pb-0">
            <a href="/">
              <h1 className="title-page ls-3 fw-bold text-current font-xxl">
                UNITY
                <span className="d-block font-xsssss ls-1 text-secondary open-font">
                  Online Course
                </span>
              </h1>
            </a>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav nav-menu float-none text-center">
                <NavItem name="Home" link="/" />
                <NavItemDropDown name="Pages" links={pages_links}/>
                <NavItemDropDown name="Blog" links={blog_links}/>
                <NavItemDropDown name="Course" links={course_links}/>
                <NavItem name="Users" link="users" />
                <NavItem name="Cards" link="cards" />
              </ul>
            </div>
          </div>
          <div className="col-lg-3 text-right d-none d-lg-flex justify-content-between flex-row-reverse align-items-center">
            <a href="#" data-toggle="modal" data-target="#ModalCart" className="float-right text-center mt-1 ml-4 text-secondary position-relative">
              <FontAwesomeIcon icon={faShoppingCart} />
              <span className="font-xssss fw-500 d-block lh-1">Carrito</span>
            </a>
            <a href="#" className="float-right text-center mt-1 ml-4 text-secondary position-relative">
              <FontAwesomeIcon icon={faHeart} />
              <span className="font-xssss fw-500 d-block lh-1">MeGusta</span>
            </a>
            <a href="#" className="float-right d-none d-lg-block text-center mt-1 ml-4 text-secondary">
              <FontAwesomeIcon icon={faUser} />
              <span className="font-xssss fw-500 d-block lh-1">Perfil</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
