import { useState, useEffect } from "react";
import axios from "axios";

export default function Cards() {
  const baseURL = "https://randomuser.me/api/?page=1&results=10";

  const [users, setUsers] = useState([]);

  const grid_styles = {
    display: 'grid',
    alignItems: 'center',
    gridTemplateColumns: 'repeat(auto-fit, minmax(240px, 1fr))',
    rowGap: '50px',
    columnGap: '60px'
  }

  useEffect(() => {
    axios.get(baseURL).then((response) => {
      setUsers(response.data.results);
    });
  }, []);

  return (
    <main>
      <div className="container">
        <h1 className="title-page">Cards</h1>
        <div style={grid_styles}>
          {
            users.map(function (user, i) {
              const name = user.name.first + ' ' + user.name.last;

              return (
                <div class="card">
                  <div style={{padding: '20px'}}>
                    <img src={user.picture.large} style={{height: '100px',borderRadius: '50%', margin: '0 auto'}} class="d-block" alt={"user" + i} />
                  </div>
                  <div class="card-body">
                    <h5 class="card-title text-center">{name}</h5>
                    <p class="card-text">

                    </p>
                    <p class="card-text">
                    </p>
                    <a href="#" class="btn btn-primary d-block">Call</a>
                  </div>
                </div>
              );
            })
          }
        </div>
      </div>
    </main>
  );
}